.card {
    background-color: rgba(255, 255, 255, 0.2);
    box-shadow: 0 4px 10px -4px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: 4px 4px 10px -4px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0 4px 10px -4px rgba(0, 0, 0, 0.1);
    display: flex;
    margin: 20px 0;
    border-radius: 1rem;
    height: 100px;
    align-items: center;
    padding: 10px 75px;
    
}

.weekday {
    font-size: 1.5rem;
    color: #eee;
    width: 18%;
}

.details {
    display: flex;
    gap: 70px;
    margin-left: 50px;
}